body {
  background-color: #dddddd;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
}

h2 {
  margin-top: 5px;
  margin-bottom: 5px;
}

/* --- Search form --- */
input[type="text"] {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}
#search-type-selection label {
  display: inline-block;
  min-width: 8rem;
  margin-left: 5px;
  text-align: left;
}
button[type="submit"] {
  padding: 5px 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.emoji {
  margin-right: 5px;
}
/* ------------------- */

/* --- Error box --- */
.error-box {
  background-color: rgba(255, 0, 0, 0.2);
  display: inline-block;
  padding: 0 20px;
  border-radius: 4px;
}
/* ----------------- */

/* --- Search results --- */
#search-results {
  /*width: 100%;*/
  display: grid;
  grid-template-columns: repeat(7, auto) minmax(150px, auto);
  grid-gap: 5px;
  /*justify-content: start;*/
  align-items: center;
  /*box-sizing: border-box;*/
}
#search-results .heading {
  font-weight: 700;
}
.flag {
  width: 100px;
  border: 1px solid rgba(0, 0, 0, 0.5);
}
/* ---------------------- */

/* --- Statistics box --- */
#statistics-box {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 5px;
  margin: 0 auto;
  width: 400px;
  text-align: left;
}
#statistics-box .region,
#statistics-box .subregion {
  display: block;
}
/* ---------------------- */
